.box-area {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
}
.box-area li {
	position: absolute;
	display: block;
	list-style: none;
	width: 25px;
	height: 25px;
	background: rgba(98, 68, 206, 0.6) ;
	-webkit-animation: animate 20s linear infinite;
	        animation: animate 20s linear infinite;
	bottom: -150px;
}
.box-area li:nth-child(1) {
	left: 86%;
	width: 80px;
	height: 80px;
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
}
.box-area li:nth-child(2) {
