.roadMap_content h2{
    
    margin: 0;
    padding: 0;
    font-size: 3em;
    color: #000;
    font-size: bold;
    text-align: center;
}
.roadMap_content p{
    color:#000;
    margin: 0;
    padding-top: 10px;
    font-size: 1.5em;
    text-align: justify;
    
}
@media screen and(max-width:350px ) {
    #roadMAp-text{
        min-width: 90%;
    } 
    
}
